/**
 * SEO component that queries for data with
 *  Gatsby's useStaticQuery React hook
 *
 * See: https://www.gatsbyjs.org/docs/use-static-query/
 */

import React from "react";
import PropTypes from "prop-types";
import { Helmet } from "react-helmet";
import { PageProps, useStaticQuery, graphql } from "gatsby";

const SEO = () => {
	const { site, social_photo } = useStaticQuery(
		graphql`
			query {
				site {
					siteMetadata {
						title
						description
						author
						url
					}
				}

				social_photo: file(relativePath: { eq: "social-icons.jpg" }) {
					publicURL
				}
			}
		`
	);

	const metaDescription = site.siteMetadata;

	return (
		<Helmet
			title={metaDescription.title}
			titleTemplate={`%s | ${site.siteMetadata.title}`}
			meta={[
				{
					name: `description`,
					content: metaDescription.description,
				},
				{
					property: `og:title`,
					content: metaDescription.title,
				},
				{
					property: `og:description`,
					content: metaDescription.description,
				},
				{
					property: `og:image`,
					content: metaDescription.url + social_photo.publicURL,
				},
				{
					property: `og:type`,
					content: `website`,
				},
				{
					name: `twitter:card`,
					content: `summary`,
				},
				{
					name: `twitter:creator`,
					content: site.siteMetadata.author,
				},
				{
					name: `twitter:title`,
					content: metaDescription.title,
				},
				{
					name: `twitter:description`,
					content: metaDescription.description,
				},
				{
					property: `twitter:image`,
					content: metaDescription.url + social_photo.publicURL,
				},
			]}
		/>
	);
};

export default SEO;
